@import-normalize;
@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: 'PT Sans Narrow';
  font-style: normal;
  font-weight: 400;
  src: url('./assets/fonts/pt-sans-narrow-v18-cyrillic_latin-regular.woff2') format('woff2');
}

@font-face {
  font-family: 'PT Sans Narrow';
  font-style: normal;
  font-weight: 700;
  src: url('./assets/fonts/pt-sans-narrow-v18-cyrillic_latin-700.woff2') format('woff2');
}

html {
  height: 100%;
}
body {
  height: 100%;

  font-family: 'PT Sans Narrow', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ffffff;
  color: #0c243e;
}
* {
  box-sizing: border-box;
}

#root {
  height: 100%;
}

a {
  text-decoration: none;
}

a:active,
a:focus {
  outline: none;
}

button:active,
button:focus {
  outline: none;
}

input:focus {
  outline: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0;
}

#root {
  height: 100%;
}
